import { Link } from "react-router-dom";
import '../Header/Header.scss'
import { logo } from '../../utils/baseIcons'

const Header = () => {
    return (
        <header className="header">
            <Link to='/'>
                <img src={logo} alt="logo" />
            </Link>
            <nav className="header__nav">
                <Link to='/'>
                    <h4></h4>
                </Link>
                <Link to='/projects'>
                    <h4> </h4>
                </Link>
                <Link to='/services'>
                    <h4></h4>
                </Link>
            </nav>
        </header >

    );
}

export default Header;