import './About.scss'
import { Link } from 'react-router-dom'
import maingimg from '../../images/about__mainimg.png'
import skills from '../../images/.svg'
const About = () => {
    return (
        <section className="about">
            <div className="about__text-wrapper">
                <h2 className="about__title"> </h2>
                <p className="about__text">  ,  - -         !</p>
                <h3 className="text__subtitle">      : </h3>
                <ul className="text__list">
                    <li className="list__item"><span className="list__header first">1</span>       -     ,    . </li>
                    <li className="list__item"><span className="list__header second">2</span>     -  ,   -   </li>
                    <li className="list__item"><span className="list__header third">3</span>  ,     . ,       ,    
                    </li>
                </ul>
                <img src={skills} alt="" className="about__image-row" />
                <Link to='/form' className='about__link'> </Link>
            </div>
            <img src={maingimg} alt="" className="about__mainimage" />
        </section>
    );
}

export default About;